import React, {useEffect} from "react";
import Profile from "./Profile";
import {connect} from "react-redux";
import {toogleIsLoading} from "../../../redux/UsersReducer";
import {withRouter} from "react-router-dom";
import {getStatus, updateStatus, getUserProfile} from "../../../redux/ProfileReducer";
import withAuthRedirect from "../../../hoc/withAuthRedirect";
import {compose} from "redux";
import {setInitialized} from "../../../redux/appReducer";


class ProfileContainer extends React.Component {
    componentDidMount() {
        let userId = this.props.match.params.userId;
        if (!userId) {
            userId = this.props.authorizedUserId;
            if (!userId) {
                this.props.history.push("/login");
            }
        }
        this.props.getUserProfile(userId);
        this.props.getStatus(userId);
    }

    render() {
        return (
            <Profile {...this.props}
                     profile={this.props.profile}
                     status={this.props.status}
                     updateStatus={this.props.updateStatus}/>
        )
    }


}

let mapStateToProps = state => {
    return {
        profile: state.Profile.profile,
        status: state.Profile.status,
        authorizedUserId: state.auth.mainUserId,
        isAuth: state.auth.isAuth
    }
}
export default compose(
    withRouter,
    withAuthRedirect,
    connect(mapStateToProps, {toogleIsLoading, setInitialized, getStatus,updateStatus, getUserProfile})
)(ProfileContainer);